export async function getIndexPatterns(savedObjectsClient) {
  return savedObjectsClient.find({
    type: 'index-pattern',
    fields: ['title', 'type'],
    perPage: 10000
  }).then(response => response.savedObjects.map(pattern => {
    const id = pattern.id;
    const title = pattern.get('title');
    return {
      id,
      title,
      // the prepending of 0 at the default pattern takes care of prioritization
      // so the sorting will but the default index on top
      // or on bottom of a the table
      sort: `${title}`
    };
  }).sort((a, b) => {
    if (a.sort < b.sort) {
      return -1;
    } else if (a.sort > b.sort) {
      return 1;
    } else {
      return 0;
    }
  })) || [];
}
export async function getPits(client, title) {
  if (title) {
    const savedObjects = await client.find({
      type: 'point-in-time',
      perPage: 1000,
      fields: ['id']
    });
    return savedObjects.savedObjects;
  }
}
export async function findByTitle(client, title) {
  if (title) {
    const savedObjects = await client.find({
      type: 'point-in-time',
      perPage: 1000,
      fields: ['id']
    });
    return savedObjects.savedObjects.find(obj => obj.attributes.id.toLowerCase() === title.toLowerCase());
  }
}
